*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
}
a{
  color: #fff;
  text-decoration: none;
}
.container{
  max-width: 1400px;
  margin: 0 auto;
}

.weather__headline {
  font-size: 64px;
  font-weight: 500;
  text-align: center;   
}
.headline {
}
.headline__title {
}

header{
  padding: 0px 20px;
  position: relative;
}

.header__top {
  width: 100%;
  background-color: #fff;
  font-size: 1,25rem;
  border-bottom: 2px solid #333;
  padding: 10px 0px ;

  display: flex;
  justify-content: space-between;
}

.header__user-location {}

.header__main {
  background-color: transparent;
  
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 10px 0px ;
}

.header__logo {
  font-size: 28px;
}

.header__nav {
  display: flex;
  align-items: center;
  gap: 40px;
  max-width: 600px;
  cursor: pointer;

  z-index: 99;
}

.header__links {
  box-shadow: inset 0 0 0 0 #333;
  color: #333;
  text-decoration: none;
  padding: 0 .25rem;
  margin: 0 -.25rem;
  transition: color .5s ease-in-out, box-shadow .5s ease-in-out;
  position: relative;
}

.header__links span{
  display: inline-block;
  width: 10px;
  height: 10px;
  background-image: url('../icons/icons-stuff/arrow-right.svg');
  background-size: contain;
  transform: rotate(90deg);
  transition: .3s;
}

.hide{
  display: none;
}
.header__dropdown{
  position: absolute;
  top: 30px;
  list-style: none;
  background-color: #333;
}
.header__dropdown li{
  padding: 20x;
}
.header__dropdown li:hover{
  background-color: #E1E1E1;
}

.header__links:hover {
  color: #fff;
  box-shadow: inset 200px 0 0 0 #333;
}
.header__links-active{
  color: #fff;
  box-shadow: inset 200px 0 0 0 #333;
}
.header__burger{
  display: none;
  flex-direction: column;
  /* line-height: 1.5; */
  gap: 5px;
  padding: 10px;
}

.header__burger span{
  display: block;
  width: 40px;
  height: 5px;
  background-color: #333;
  border-radius: 5px;
}

.header__button{
  width: 100px;
  height: 30px;

  background: transparent;

  border: 1px solid #070707;
  border-radius: 10px;
  transition: 1s;
}

.header__button:hover{
  box-shadow: inset -10.5em 0 0 0 #333, inset 10.5em 0 0 0 #333;
  color: #fff;
}

