/* WEATHER START */

.weather{
  background: linear-gradient(136deg, #E1E1E1 0%, rgba(225, 225, 225, 0.00) 100%);
}

.weather__headline {
}
.headline {
  padding: 100px 0px 50px;
}
.headline__title {
}


.weather__main {
  /* max-width: 800px; */
  padding-bottom: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.weather__location {
  display: flex;
  gap: 20px;
}

.weather__location input, 
.weather__location button{
  border: 1px solid #33CCCC;
  border-radius: 10px;
  font-size: 20px;
  height: 40px;
}

.weather__location input{
  width: 200px;
  padding: 10px;
}

.weather__location button{
  width: 150px;
  background-color: transparent;

  transition: all .3s;
  cursor: pointer;
}

.weather__location button:hover{
  background-color: #33CCCC;
  color: #fff;
}

.weather__forecast {
  width: 60%;
}

.weather__forecast-top{
  padding: 40px 0px 20px;
}

.weather__choose {
  font-size: 1.7rem;

  max-width: 250px;
  min-height: 50px;

  display: flex;
  justify-content: space-between;

  background-color: #fff;
  border-radius: 10px;

  overflow: hidden;
}

.weather__block{
  display: inline-block;
  padding: 10px 15px;
  transition:  background .3s;
}

.weather__forecast-main{
  width: 100%;
  min-height: 200px;

  display: flex;
  align-items: center;
  justify-content: space-around;

  padding: 20px 40px;

  background-color: #fff;
  border:1px solid #333;
  border-radius: 20px;
  overflow: hidden;

  font-size: 32px;
}
.weather__city {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.weather__city-icon {
  background-image: url('../icons/icons-weather/clouds.gif');
  background-repeat: no-repeat;
  background-size: contain;
  width: 150px;
  height: 150px;
}
.weather__city-decription {
}
.weather__city-name {
}
.weather__additional {
  padding: 0px 30px;
  border-right:2px solid #070707 ;
  border-left: 2px solid #070707;
}
.weather__additional div{
  border-bottom: 1px solid #070707;
}
.weather__temperature, .weather__city-name {
  font-size: 56px;
}

.weather__block--active{
  border-radius: 10px;
  box-shadow: 0px 0px 10px #33CCCC;
  background-color: #33CCCC;
  color: #fff;
}


/* WEATHER END */

